export default {
	data() {
		return {
			feriends: {}
		};
	},
	onLoad(e) {
		this.WinCalculation();
	},
	methods: {
		// ListTouch触摸开始
		WinCalculation() {
			var arrList = this.$WinList;
			var user = [];
			for (var i in arrList) {
				var feriend = arrList[i].LstFriends.split('-');

				for (var j in arrList) {
					if (j < 3) {
						if (feriend[j] == '未填写' || feriend[j] == '汇总' || feriend[j] == '') {
						} else {
							console.log(feriend[j] + '===' + j.toString());
							user.push(feriend[j]);
						}
					}
				}
			}
			console.log(user);
			// 			//去重
			let arr = user;
			let result = [];
			let obj = {};

			for (let i of arr) {
				if (!obj[i]) {
					result.push(i);
					obj[i] = 1;
				}
			}

			console.log(result);
			this.feriends = result;
		}
	}
};
